  .table table tbody {
    max-height: 1054px;
  }
}
@media (max-width: 382px) {
  .table {
    margin-right: 16px;
  }
  .table p {
    margin-right: 10px;
  }
}
@media (max-width: 374px) {
  .table table thead th {
    font-size: 12px;
  }
  .table table tbody {
    max-height: 648px;
  }
  .table table tbody td {
    font-size: 12px;
  }
  .table table th:first-of-type,
.table table td:first-of-type {
    width: 66px;
  }
  .table table th:first-of-type + th,
.table table td:first-of-type + td {
    width: 80px;
  }
}
@media (max-width: 340px) {
  .table p span:first-of-type {
    font-size: 20px;
  }
  .table p span:last-of-type {
    font-size: 16px;
  }
}
@media (max-width: 326px) {
  .table table tbody {
    max-height: 856px;
  }
}

